.py-2-5 {
	padding-top: 0.75rem !important;
	padding-bottom: 0.75rem !important;
}

.h5-5 {
	font-size: 16px;
}

.drpdw-none::after {
	content: none !important;
}

.truncate-overflow-1 {
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.truncate-overflow-2 {
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.truncate-overflow-3 {
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.truncate-overflow-10 {
	display: -webkit-box;
	-webkit-line-clamp: 10;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.slider-image {
	height: 320px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.image-slider {
	margin-top: 10px;
	width: 90%;
	border-radius: 10px;
}

@media (max-width: 900px) {
	.slider-image {
		height: 250px;
	}
}
@media (max-width: 640px) {
	.slider-image {
		height: 170px;
	}
	.w-95 {
		width: 95%;
	}
}

@media (max-width: 480px) {
	.slider-image {
		height: 150px;
	}
	.image-slider {
		margin-top: 10px;
		width: 95%;
		border-radius: 10px;
	}
}

.swiper-pagination > .swiper-pagination-bullet {
	opacity: 1;
	background-color: #c2d6ca;
}
.swiper-pagination > .swiper-pagination-bullet-active {
	background-color: #6270fb;
	/* background-color: #4b4d4e; */
}

.text-grayy {
	color: #f1f1f1;
}

.text-grayy:hover {
	color: #f1f1f1;
}
.text-grayi {
	color: #c8cbcc;
}
.text-grayi:hover {
	color: #c8cbcc;
}

.dark-text {
	color: #212529;
}

.dark-text:hover {
	color: #1d2024;
}

.subcat-badge:hover {
	background-color: #6270fb;
	color: white;
}

/* Footer */

.context-dark,
.bg-gray-dark,
.bg-primary {
	color: rgba(255, 255, 255, 0.8);
}

.footer-classic a,
.footer-classic a:focus,
.footer-classic a:active {
	color: #ffffff;
}
.nav-list li {
	padding-top: 5px;
	padding-bottom: 5px;
}

.nav-list li a:hover:before {
	margin-left: 0;
	opacity: 1;
	visibility: visible;
}

.social-inner {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	padding: 23px;
	font: 900 13px/1 "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
		"Helvetica Neue", Arial, sans-serif;
	text-transform: uppercase;
	color: rgba(255, 255, 255, 0.5);
}
.social-container .col {
	border: 1px solid rgba(255, 255, 255, 0.1);
}
.nav-list li a:before {
	content: "\f14f";
	font: 400 21px/1 "Material Design Icons";
	color: #4d6de6;
	display: inline-block;
	vertical-align: baseline;
	margin-left: -28px;
	margin-right: 7px;
	opacity: 0;
	visibility: hidden;
	transition: 0.22s ease;
}

.subcat-badge {
	color: #21252f;
	background-color: #fff;
}

.subcat-badge:hover {
	background-color: #fff;
	color: #21252f;
}

.subcat-badge-active {
	background-color: #4d6de6;
	color: #fff;
}

.subcat-badge-active:hover {
	color: #fff;
	background-color: #2d55e6;
}

.pp {
	text-decoration: none;
	background-image: linear-gradient(to right, #6270fb, #4653eb);
	background-position: bottom left;
	background-repeat: no-repeat;
	background-size: 0% 2px;
	transition: background-size 0.5s ease;
}

.pp:hover {
	background-size: 100% 2px;
}

.pp-active {
	text-decoration: none;
	background-image: linear-gradient(to right, #6270fb, #4653eb);
	background-position: bottom left;
	background-repeat: no-repeat;
	background-size: 100% 2px;
	transition: background-size 0.5s ease;
}
@media (min-width: 992px) {
	.modal-cxl {
		max-width: 900px;
	}
}
@media (max-width: 992px) {
	.modal-cxl {
		max-width: 900px;
		max-height: 90%;
	}
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-color: #888;
}

::-webkit-scrollbar-track {
	background: #f1f1f1;
	border-left: 1px solid #aaa;
}

.blur-bg {
	/* background: rgba(255, 255, 255, 0.3);
	box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
	backdrop-filter: blur(12px);
	-webkit-backdrop-filter: blur(12px);
	border-radius: 10px;
	border: 1px solid rgba(255, 255, 255, 0.18); */
	background: rgba(255, 255, 255, 0.5);
	box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
	backdrop-filter: blur(4px);
	-webkit-backdrop-filter: blur(4px);
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	border: 1px solid rgba(255, 255, 255, 0.18);
}

.close-btn:hover {
	color: #4653eb;

	transform: rotate(90deg);
	-webkit-transform: rotate(90deg);
}

.close-btn {
	color: #6270fb;
	transition: opacity 300ms ease,
		transform 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
		-webkit-transform 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.scroll-y::-webkit-scrollbar {
	display: none;
}
.scroll-y {
	-ms-overflow-style: none; /* IE and Edge */
	scrollbar-width: none;
	/* Firefox */
}

.like-btn {
	transition: all 0.3s ease-in-out;
}

.like-btn:hover {
	transform: scale(1.2);
}

.dropdown-hover:hover > .dropdown-menu {
	display: inline-block;
}

.dropdown-hover > .dropdown-toggle:active {
	/*Without this, clicking will make it sticky*/
	pointer-events: none;
}

.instagram {
	background: #f09433;
	background: -moz-linear-gradient(
		45deg,
		#f09433 0%,
		#e6683c 25%,
		#dc2743 50%,
		#cc2366 75%,
		#bc1888 100%
	);
	background: -webkit-linear-gradient(
		45deg,
		#f09433 0%,
		#e6683c 25%,
		#dc2743 50%,
		#cc2366 75%,
		#bc1888 100%
	);
	background: linear-gradient(
		45deg,
		#f09433 0%,
		#e6683c 25%,
		#dc2743 50%,
		#cc2366 75%,
		#bc1888 100%
	);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
}

.btn-outline-dark-blue {
	color: #212529;
	border-color: #0d6efd;
}
.btn-outline-dark-blue:hover {
	color: #fff;
	background-color: #0d6efd;
	border-color: #0d6efd;
}

::-webkit-scrollbar {
	width: 15px;
}

::-webkit-scrollbar-track {
	background-color: transparent;
	border: none;
}

::-webkit-scrollbar-thumb {
	/* background-color: #d6dee1; */
	background-color: #bec1e6;
	border-radius: 20px;
	border: 3px solid transparent;
	background-clip: content-box;
	transition: all 300ms ease-in-out;
}
::-webkit-scrollbar-thumb:hover {
	background-color: #6270fb;
	transform: scale(1.2);
}
